.outside_container {
    overflow: hidden;
    background-color: greenyellow;
    height: 100vh;
}

.container_main {
    width: 80%;
    height: calc(100vh - 0px);
    margin: auto;
    /* background-color: grey; */
    /* padding: 20px; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.ensemble_dessin {
    width: 100%;
    height: 30%;
    /* background-color: wheat; */
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.galerie_icon {
    width: 90%;
    /* background-color: olive; */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.button_icon {
    /* background-color: red; */
    background-color: transparent;
    /* font-size: 10vw; */
    width: 20%;
    cursor: pointer;
    border: none;
    transition: transform 1s;
    font-size: 7vw;
}

.button_icon:hover {
    transform: scale(2.5);
}

.faRocket {
    transform: rotate(-45deg);
    /* background-color: green; */
    position: relative;
    top: 1vw;
    /* background-color: violet; */
}

.faShuttleSpace {
    transform: rotate(-90deg);
    position: relative;
    top: -0.7vw;
}

.faHelicopter {
    transform: rotate(0deg);
}

.duck {
    transform: rotate(0deg);
}

.floor {
    width: 100%;
    height: 5px;
    background-color: black;
    text-align: center;
    color: white;
}

.floor2 {
    /* position:relative; */
    /* top: 20px; */
    /* left: 50%; */
    /* transform: translateX(-50%); */
    /* margin-top: 10vh; */
    height: 0;
    width: 90%;
    margin: auto;
    border-bottom: 50px solid black;
    border-left: 30px solid transparent;
    border-right: 30px solid transparent;
}

.button_click_incon {
    margin: 10vw;
    cursor: pointer;
}

.picture2 {
    width: 70%;
    background-color: brown;
    display: flex;
    flex-direction: column;
    padding: 50px;
}

.icon_alone {
    margin: auto;
    background-color: violet;
    font-size: 13.5vw;
