    .container_all_entree {
        width: 70%;
        height: 100vh;
        /* background-color: grey; */
        margin-top: 10vw;
        margin: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        overflow: hidden;
        position: relative;
    }
    
    .input_center {
        position: absolute;
        top: calc(50% - 1em);
        left: calc(50% - 3% - 30px);
        width: calc(6% + 60px);
        height: 2em;
        font-size: 2em;
        text-align: center;
        border: none;
        background-color: transparent;
        cursor: pointer;
        /* border: solid black 2px; */
    }
    
    /* @media (max-width: 1000px) {
        .input_center {
            font-size: 1em;
        }
    } */
    /* .input_center {} */
    
    .button_center {
        position: absolute;
        top: calc(50% - 1em);
        left: 45%;
        width: 10%;
        left: calc(50% - 3% - 30px);
        width: calc(6% + 60px);
        height: 2em;
        font-size: 2em;
        text-align: center;
        border: none;
        background-color: black;
        background-color: transparent;
        cursor: pointer;
        /* border: solid black 2px; */
        /* border-radius: 2%; */
        transition-duration: 0.4s;
        border-radius: 4px;
    }
    
    .button_center:hover {
        background-color: black;
        /* Green */
        color: white;
    }
    
    .button_center3 {
        position: absolute;
        top: calc(50% - 1em);
        left: calc(50% - 1em);
        width: 2em;
        height: 2em;
        font-size: 2em;
        text-align: center;
        border: none;
        background-color: white;
        border: black solid 2px;
        /* background-color: transparent; */
        cursor: pointer;
        border-radius: 50%;
        display: block;
    }
    
    .letter_center {
        position: absolute;
        /* top: calc(50% - 20%); */
        /* width: 2%; */
        /* left: calc(50% - 0%); */
        /* height: 40%; */
        /* background-color: hotpink; */
        background-color: transparent;
        font-size: 2em;
    }

    @media (max-width: 1000px) {
        .letter_center {
            font-size: 1em;
        }

        .container_all_entree{
            width: 100%;
        }
    }
    
    .square_test {
        background-color: rgba(224, 224, 87, 0.5);
        height: 500px;
        width: 500px;
        position: absolute;
        top: calc(50% - 250px);
        left: calc(50% - 250px);
        border-radius: 50%;
    }
    
