/* CSS GLOBAL*/


/* CSS GLOBAL*/


/* CSS GLOBAL*/

:root {
    --color1: #0065FC;
    --color2: #DEEBFF;
    --color3: #F2F2F2;
    --color4: #000000;
}


/* @font-face {
    font-family: 'raleway';
    src: url('./Police_Raleway/Raleway-Regular.ttf');
  } */

.blue {
    color: var(--color1);
}

.grey {
    color: var(--color3);
}

.black {
    color: var(--color4);
}

.blueback {
    background-color: var(--color1);
}

.greyback {
    background-color: var(--color3);
}

.bold {
    font-weight: bold;
}

div.body {
    font-family: 'raleway', arial;
    margin: 0px;
    padding: 0px;
}

h1 {
    color: black;
    font-weight: bold;
}

html {
    scroll-behavior: smooth;
}

.block {
    width: auto;
    height: auto;
    display: inline-block;
}

.lien {
    font-weight: bold;
    font-style: normal;
    text-decoration: none;
}

h3,
p {
    padding: 0px;
    margin: 0px;
}

h3 {
    font-weight: bold;
}


/* HEADER */


/* HEADER */


/* HEADER */

.container_header {
    width: calc(100% - 60px);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-end;
    margin: 0px 30px;
}

.container_header>div {
    display: flex;
    align-items: center;
}

.header_logo {
    width: 100px;
    padding: 0px 0px 0px 20px;
}

#img_logo {
    width: 100%;
    height: auto;
}

.nav_space {
    height: 10px;
    flex: 1;
}

.nav_lien {
    min-width: 50px;
    display: flex;
    justify-content: flex-end;
    order: 5;
}

.nav_anchor,
.nav_lien {
    width: auto;
    padding: 20px 0px;
    margin: 0px 30px;
}

div.nav_anchor.active {
    border-top: 3px var(--color1) solid;
}

.nav_anchor.active>a {
    color: var(--color1);
}

.nav_anchor>a {
    color: black;
}


/* bloc_page */

#bloc_page {
    width: calc(100% - 60px);
    display: flex;
    flex-direction: column;
    margin: 0px 30px 20px 30px;
}


/* Part0 */


/* Part0 */


/* Part0 */

#Part0 {
    width: calc( 100% - 30px);
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0px 15px 0px 15px;
}

#Part0>div {
    margin: 10px 0px;
}

.button_de_recherche {
    max-width: 400px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 40px;
    border-radius: 11px;
}

.button-icone {
    height: 45px;
    width: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-bottom-left-radius: 11px;
    border-top-left-radius: 11px;
}

.icone_lieu {
    position: relative;
}

.button-champs {
    height: 45px;
    padding: 0px 0px 0px 25px;
    border: 2px var(--color3) solid;
    flex: 1;
    max-width: 225px;
}

.button-clic {
    height: 45px;
    min-width: 45px;
    border: inherit;
    border-bottom-right-radius: 11px;
    border-top-right-radius: 11px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
    left: -10px;
}

#ville {
    font-weight: bold;
    font-size: 14px;
}

.text_search {
    width: 90px;
    font-weight: bold;
    font-size: 14px;
}

.icone_search {
    width: 0;
    color: var(--color1);
}

.container_filtre {
    width: auto;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.filtre_titre {
    width: auto;
    height: auto;
    padding: 5px 30px 5px 5px;
}

.filtre {
    width: auto;
    height: 40px;
    display: flex;
    align-items: center;
    border: 2px var(--color3) solid;
    border-radius: 100px;
    margin: 5px 20px 5px 0px;
}

.blue_circle {
    width: 42px;
    height: 42px;
    background-color: var(--color2);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    right: 2px;
}

.filtre:hover {
    background-color: var(--color2);
    border: 2px var(--color1) solid;
}

.filtre:hover .blue_circle {
    background-color: var(--color1);
}

.filtre:hover .blue-hover {
    color: var(--color2);
}

.filtre_text {
    color: var(--color4);
    padding: 0px 10px 0px 10px;
}

.container_filtre_2 {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: flex-start;
    align-items: baseline;
}

.information_icone {
    color: var(--color1);
    border: 3px var(--color3) solid;
    border-radius: 100px;
    padding: 4px 9px;
}

.texte_info {
    padding: 10px;
}


/* PART1_2 */

#PART1_2 {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}


/* Hebergements */


/* Hebergements */


/* Hebergements */

.container-hebergement {
    min-width: 300px;
    width: 62%;
    padding: 10px 40px 20px 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 20px;
    background-color: var(--color3);
}

.collection_1 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.container_modele_1 {
    min-width: 225px;
    width: 30%;
    height: auto;
    padding: 0px 0px 25px 0px;
}

.carte-modele-1 {
    width: 100%;
    padding-bottom: 82%;
    display: flex;
    flex-direction: column;
    border: 5px white solid;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 0px 25px rgb(223, 223, 223);
    position: relative;
}

.carte-modele-1>div {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.carte-modele-1-img {
    width: 100%;
    height: 64%;
    object-fit: cover;
    border-top-left-radius: 11px;
    border-top-right-radius: 11px;
}

.carte-modele-1-txt {
    height: 33%;
    padding: 0px 0px 10px 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}


/* titre_populaire */


/* titre_populaire */


/* titre_populaire */

.container-populaire {
    min-width: 300px;
    width: 28%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    border-radius: 20px;
}

.collection_2 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.container_modele_2 {
    min-width: 290px;
    width: calc(100% - 10px);
    height: auto;
    padding: 0px 0px 25px 0px;
}

.carte-modele-2 {
    width: 100%;
    padding-bottom: 40%;
    border: 5px white solid;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 0px 25px rgb(223, 223, 223);
    position: relative;
}

.carte-modele-2>div {
    display: flex;
    flex-direction: row;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.carte-modele-2-img {
    width: 40%;
    height: 100%;
    object-fit: cover;
    border-top-left-radius: 11px;
    border-bottom-left-radius: 11px;
}

.carte-modele-2-txt {
    min-width: 100px;
    margin: 10px 0px 10px 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.titre_pop {
    width: calc(100% + 0px);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}


