@media screen and (max-width: 1320px) {
    #PART1_2 {
        width: 100%;
        height: auto;
        display: flex;
        flex-wrap: wrap-reverse;
    }
    .container-hebergement {
        width: 100%;
        margin: 0px;
    }
    .container-populaire {
        width: 100%;
        margin: 0px 0px 20px 0px;
    }
    .collection_2 {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .column_modele_3 {
        min-width: 190px;
    }
    .container_modele_2 {
        width: 31%;
    }
}

@media screen and (min-width: 880px) and (max-width: 1020px) {
    .collection_2 {
        justify-content: space-around;
    }
    .container_modele_2 {
        width: 35%;
    }
    .column_modele_3 {
        width: 46%;
        margin: 0px 0px 20px 0px;
    }
}

@media screen and (min-width: 880px) and (max-width: 1230px) {
    .column_modele_3 {
        width: 46%;
        margin: 0px 0px 20px 0px;
    }
}

@media screen and (min-width: 630px) and (max-width: 880px) {
    .container_modele_1 {
        width: 45%;
    }
    .collection_2 {
        justify-content: space-around;
    }
    .container_modele_2 {
        width: 35%;
    }
    .column_modele_3 {
        width: 46%;
        margin: 0px 0px 20px 0px;
    }
    .container_footer>div {
        width: 40%;
        margin: 10px;
    }
}

@media screen and (max-width: 790px) {
    .container_modele_2 {
        width: 75%;
        max-width: 450px;
        min-width: 250px;
    }
    .collection_2 {
        min-width: 250px;
        display: flex;
        flex-direction: column;
        align-items: center;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    .column_modele_3 {
        margin: 0px 0px 20px 0px;
    }
    .container_footer>div {
        width: 51%;
        margin: 10px;
    }
}

@media screen and (max-width: 730px) {
    .nav_lien {
        order: 3;
        width: 40%;
        padding: 0px;
        margin: 0px;
        display: flex;
        justify-content: center;
    }
    .nav_space {
        width: 0;
    }
    .nav_anchor {
        width: 50%;
        margin: 20px 0px 0px 0px;
        padding: 0px 0px 20px 0px;
        order: 4;
        display: flex;
        justify-content: center;
    }
    div.nav_anchor {
        border-bottom: 5px var(--color3) solid;
    }
    div.nav_anchor.active {
        border-top: 3px var(--color1) none;
        border-bottom: 3px var(--color1) solid;
    }
}

